import React from 'react'
import titleStyle from './title.module.sass'

class Title extends React.Component{
  constructor (props) {
    super();
    this.state = {
      text: props.text
    }
  }

  render () {
    return (
      <div className={titleStyle.titleWrap}>
        <h2>{ this.state.text }</h2>
        <div className={titleStyle.dash}></div>
      </div>
    )
  }
}

export default Title;
