import { CHANGE_COURSE } from '../actionTypes'

const initialState = {
  content: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case CHANGE_COURSE: {
      const { content } = action.payload;
      return {
        ...state,
        content
      };
    }
    default:
      return state;
  }
}
