import React from 'react'
import { Link } from 'react-router-dom'

import headerStyle from './header.module.sass'

import { Menu, Icon } from 'antd';

const { SubMenu, Item } = Menu;

class Header extends React.Component{
  _menuList = [{
    title: '首页',
    href: '/',
    subList: []
  }, {
    title: '专题课程',
    href: '',
    subList: [
      {
        title: 'CS301A 算法基础快速入门(Java/Cpp)',
        href: 'course/CS301A',
        subList: []
      }, {
        title: 'CS301B 题型技巧与算法讲解(Java/Cpp)',
        href: 'course/CS301B',
        subList: []
      }, {
        title: 'CS403A LeetCode1-300高频题详解(Java/Cpp/Python)',
        href: 'course/CS403A',
        subList: []
      }, {
        title: 'CS403B LeetCode301-600高频题详解(Java/Cpp)',
        href: 'course/CS403B',
        subList: []
      }
    ]
  }]

  _renderMenu = arrList => 
    arrList.map((item, index) => 
      <Item key={index}>
        <Link to={item.href}>{item.title}</Link>
      </Item>)

  _renderNav = () => (
    this._menuList.map((item, index) => {
      if (item.subList.length === 0) {
        return (
          <Item key={index}>
            <Link to={item.href} className="ant-dropdown-link text-gray">{ item.title }</Link>
          </Item>
        )
      } else {
        return (
          <SubMenu
            title={
              <span slot="title" class="submenu-title-wrapper">
                { item.title }
                <Icon type="down" />
              </span>
            }
          >
            { this._renderMenu(item.subList) }
          </SubMenu>
        )
      }
    })
  )

  render () {
    return (
      <div className={headerStyle.header}>
        <div>
          <img
            className={headerStyle.logo}
            src="/logo512.png"
            alt="logo"
          />
        </div>
        <Menu mode="horizontal" className={headerStyle.nav}>
          { this._renderNav() }
        </Menu>
      </div>
    )
  }
}

export default Header;
